import React, { Component } from 'react'
import {Link} from 'react-router-dom'
// import productImg from './images/product.jpg'
// import product1 from './images/product1.jpg'
import product2 from './images/product2.jpg'
import product3 from './images/product3.jpg'

class Home extends Component {
    state = {
        products: [10000, 20000, 22000, 80800, 200000, 837000, 334000, 312000, 76000, 24000, 6000, 4000]
    }
    render() {

        return (
            <div>

                <div className="slider">
                <div id="carouselExampleControls" className="carousel slide" data-ride="carousel">
  <div className="carousel-inner">
    <div className="carousel-item active slide">
      <img className="d-block w-100 slid" src={require(`./images/product${1}.jpg`)} alt="First slide"/>
    </div>
    <div className="carousel-item">
      <img className="d-block w-100" src={product2} alt="Second slide"/>
    </div>
    <div className="carousel-item">
      <img className="d-block w-100" src={product3} alt="Third slide"/>
    </div>
  </div>
  <a className="carousel-control-prev" href="#carouselExampleControls" role="button" data-slide="prev">
    <span className="carousel-control-prev-icon" aria-hidden="true"></span>
    <span className="sr-only">Previous</span>
  </a>
  <a className="carousel-control-next" href="#carouselExampleControls" role="button" data-slide="next">
    <span className="carousel-control-next-icon" aria-hidden="true"></span>
    <span className="sr-only">Next</span>
  </a>
</div>
                </div>
                <div className="filter">
                    <nav className="navbar navbar-expand-lg navbar-light bg-dark row">
                        <Link className="navbar-brand" to="#">Filter</Link>

                        <div className="col-md-2"  >
                            <select >

                                <option disabled defaultValue="">Select Category</option>
                                <option value="Mobile">Mobiles</option>
                                <option value="Mobile">Computers</option>
                                <option value="Mobile">Vehicles</option>
                                <option value="Bikes">Bikes</option>
                                <option value="Jobes">Jobs</option>
                                <option value="Services">Services</option>
                            </select>
                        </div>
                        <div className="col-md-2"  >
                            <select>

                                <option disabled defaultValue="">Select Brand</option>
                                <option value="Mobile">Mobiles</option>
                                <option value="Mobile">Computers</option>
                                <option value="Mobile">Vehicles</option>
                                <option value="Bikes">Bikes</option>
                                <option value="Jobes">Jobs</option>
                                <option value="Services">Services</option>
                            </select>
                        </div>
                        <div>
                            <p style={{ display: "inline", color: "black" }}>Price Range</p>
                        </div>
                        <form className="form-inline">


                            <input className="form-control mr-sm-1" type="search" placeholder="From" aria-label="Search" />
                            <input className="form-control mr-sm-1" type="search" placeholder="To" aria-label="Search" />

                        </form>
                    </nav>
                </div>


                <div className="products row">


                    {this.state.products.map((val, ind) => {

                        return <div key={ind} className="card pcard" style={{ width: "18rem" }}>
                            <img className="card-img-top" src={require('./images/product.jpg')} alt={"Card Image "+ind} />
                            <div className="card-body cbody">
                                <h5 className="card-title">{"RS:" + val}</h5>
                                <p>Product Name</p>
                                <p>Location</p>
                            </div>
                            <div className="card-body cbody">
                                <button className="btn btn-primary">Contact With Seller </button>

                            </div>
                        </div>

                    })}
                </div>

            </div>
        );
    }
}
export default Home;